    width: 100px;
}

#new-task-form {
    display: grid;
    grid-template-columns: minmax(auto, 150px) minmax(auto, 300px);
    grid-template-rows: repeat(5, 1fr);
    gap: 15px;
    padding: 12px;
    align-self: flex-start;
    background-color: var(--main-background-color);
    box-shadow: var(--main-box-shadow);
    border-radius: var(--main-border-radius);
}

#task-name-label,
#task-desc-label,
#task-date-label,
#task-priority-label {
    grid-column: 1 / 2;
}

#task-name-label {
    grid-row: 1 / 2;
}

#task-desc-label {
    grid-row: 2 / 3;
}

#task-date-label {
    grid-row: 3 / 4;
}

#task-priority-label {
    grid-row: 4 / 5;
}

#create-task-button {
    grid-column: 1 / 2;
    grid-row: 5 / 6;
    width: 100px;
}

#cancel-task-button {
    grid-column: 2 / 3;
    grid-row: 5 / 6;
    width: 100px;
    justify-self: end;
}`, "",{"version":3,"sources":["webpack://./src/styles/task-form.css"],"names":[],"mappings":"AAAA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8DAA8D;IAC9D,kCAAkC;IAClC,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,8CAA8C;IAC9C,kCAAkC;IAClC,wCAAwC;AAC5C;;AAEA;;;;IAII,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,YAAY;IACZ,iBAAiB;AACrB","sourcesContent":["#new-task-button {\n    width: 100px;\n}\n\n#new-task-form {\n    display: grid;\n    grid-template-columns: minmax(auto, 150px) minmax(auto, 300px);\n    grid-template-rows: repeat(5, 1fr);\n    gap: 15px;\n    padding: 12px;\n    align-self: flex-start;\n    background-color: var(--main-background-color);\n    box-shadow: var(--main-box-shadow);\n    border-radius: var(--main-border-radius);\n}\n\n#task-name-label,\n#task-desc-label,\n#task-date-label,\n#task-priority-label {\n    grid-column: 1 / 2;\n}\n\n#task-name-label {\n    grid-row: 1 / 2;\n}\n\n#task-desc-label {\n    grid-row: 2 / 3;\n}\n\n#task-date-label {\n    grid-row: 3 / 4;\n}\n\n#task-priority-label {\n    grid-row: 4 / 5;\n}\n\n#create-task-button {\n    grid-column: 1 / 2;\n    grid-row: 5 / 6;\n    width: 100px;\n}\n\n#cancel-task-button {\n    grid-column: 2 / 3;\n    grid-row: 5 / 6;\n    width: 100px;\n    justify-self: end;\n}"],"sourceRoot":""}]);
